ual en el que toda comunicación a través de los diferentes medios (televisión, cine, prensa, revistas, etc.), posee un impactante flujo de imágenes y texto en sus eternas variedades y composiciones. Por lo tanto, el lenguaje bimedia hace referencia a la combinación perfecta entre imagen y texto. Puede diseñarse en formatos audiovisuales, impresos, multimediales o digitales, por lo que se puede apreciar en comerciales de televisión, videos promocionales, documentales, anuncios (de prensa, revista o digitales), publicidad exterior, etc. Cabe anotar que en la actualidad la publicidad usa preferiblemente este este lenguaje porque posee un texto muy corto que es totalmente coherente y contextual con la imagen. "),_c('span',{staticClass:"b"},[_c('b',[_vm._v("(Costa, 2003)")])])])]),_c('div',{staticClass:"col-lg-4"},[_c('img',{staticClass:"img-a img-t my-3",attrs:{"data-aos":"zoom-in","src":require("@/assets/curso/temas/img141.png"),"alt":""}})])])])}]

